@use "mixins" as mixins;
@use "./mixins/breakpoint" as *;
.slider {
  &__arrow {
    position: absolute;
    top: 50%;
    z-index: 10;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border: 1px solid #0554f8;
    border-radius: 0.75rem;

    &--lit {
      height: 3rem;
      width: 3rem;
    }
    &--big {
      height: 4.5rem;
      width: 4.5rem;
    }
    &.swiper-button-disabled {
      pointer-events: none;
    }
    &--prev {
      left: 0;
    }
    &--next {
      right: 0;
      svg {
        transform: rotate(180deg);
      }
    }
    svg {
      height: 1.5rem;
      width: 1.5rem;
      transition: 0.4s;
      fill: none;
      stroke-width: 1.5px;
      stroke: #adc4fc;
    }
    @include mixins.hover {
      svg {
        fill: var(--green-light);
      }
    }
  }
}
